import $ from 'jquery';

function random(min, max) {
  return Math.floor(Math.random() * ((max - min) + 1)) + min;
}

export default class SequenceOfWords {
  constructor() {
    this.task = null;
    this.arr = null;
    this.data = [
      ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
      ['', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', ''],
    ];
  }
  getRandomTask() {
    this.step = random(0, 1);
    this.task = random(this.data[this.step].length, this.data[this.step].length / 3);
    this.showTask();
    return this.task;
  }

  checkAnswer(answ) {
    return answ === this.data[this.step][this.task].toLowerCase();
  }

  showTask() {
    const html = `
    <ul class="mixed-list">
    <li>${this.data[this.step][this.task - 3]}</li>
    <li>${this.data[this.step][this.task - 2]}</li>
    <li>${this.data[this.step][this.task - 1]}</li>
    </ul>
    <input type="text" name="answer" id="taskAnsw" placeholder=" ">`;
    $('#taskBody').html(html);
    $('#taskTitle').html(':');
  }
}

